import React, { Component } from 'react'
import Loader from 'react-loader-spinner'

class Posts extends Component {
    constructor(props) {
        super(props)
        this.state = {
            posts: [],
            comments: [],
            loading: false

        }
    }

    componentWillMount() {
        this.setState({ loading: false })
        fetch('https://jsonplaceholder.typicode.com/posts')
            .then(res => res.json())
            .then(data => this.setState({ posts: data }))

        fetch('https://jsonplaceholder.typicode.com/comments')
        .then(res => res.json())
        .then(data => this.setState({ comments: data, loading: true }))
    }

    expandPost(e) {
        let expand = e.target.querySelector('.comments')
        expand.classList.toggle('show-hide')
    }

    render() {

        const {loading} = this.state

        const postItems = this.state.posts.map(post => (
            <div key={ post.id }>
                <div onClick={this.expandPost} className="posts">
                    {!loading && <div className="loading">
                        <Loader type="ThreeDots" color="#00BFFF" height="80" width="80" />
                        </div> ||
                        <div>
                            <h3 className="title">{ post.title }</h3>
                            <h3 className="body">{ post.body }</h3>
                            <div className="comments">
                                <h3>Comments:</h3>
                                <br/>
                                <div className="email">
                                    <p style={{fontWeight: "600"}}></p>
                                    {
                                        this.state.comments.map(comment => {
                                            if(comment.postId === post.id) {
                                                    
                                                for(let i = 0; i < this.state.comments.length; i++) {
                                                    const text = "User: " + comment.email + `\n` + "Message: " + comment.body + `\n` + `~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~` + `\n`

                                                    return (
                                                        text.split(`\n`).map(i => <p>{i}</p>)
                                                    )
                                                        
                                                }
                                            }else {
                                                return null
                                            }
                                        })
                                    }
                                </div>
                                <br/>
                            </div>
                        </div>
                    }
                </div>
            </div>
        ))

        return (
            <>

                <div className="space-top"></div>

                <div className="postsPage">
                    <div className="header-container">
                        <span className="text1">Posts from</span>
                        <span className="text2">jsonplaceholder:</span>
                    </div>
                    
                    { postItems }
                    
                </div>

            </>
        )
    }
}


export default Posts

