import React, { Component } from 'react'
import Loader from 'react-loader-spinner'

class Albums extends Component {
    constructor(props) {
        super(props)
        this.state = {
            albums: [],
            photos: [],
            albumLoading: false,
            photosLoading: false
        }
    }

    componentWillMount() {
        this.setState({ albumsLoading: false })
        this.setState({ photosLoading: true })
        fetch('https://jsonplaceholder.typicode.com/albums')
            .then(res => res.json())
            .then(data => this.setState({ albums: data, albumsLoading: true }))

        fetch('https://jsonplaceholder.typicode.com/photos')
            .then(res => res.json())
            .then(data => this.setState({ photos: data, photosLoading: true }))
    }

    expandAlbum(e) {
        let expand = e.target.querySelector('.photos')
        expand.classList.toggle('show-hide')
    }

    render() {

        const { albumsLoading, photosLoading } = this.state

        const albumItems = this.state.albums.map(album => (
            <div key={ album.id }>
                <div onClick={this.expandAlbum} className="albums">
                    {!albumsLoading && <div classname="loading">
                            <Loader type="ThreeDots" color="#00BFFF" height="80" width="80" />
                        </div> ||
                        <div style={{width: "100%"}}>
                            <h3 className="title">{ album.title }</h3>
                            <div className="photos">
                                <h3>Photos: </h3>
                                <br/>
                                <div>
                                    <p style={{fontWeight: "600"}}></p>
                                    {
                                        this.state.photos.map(photo => {
                                            if(photo.albumId === album.id) {
                                                for(let i = 0; i < this.state.photos.length; i++) {
                                                    return (
                                                        <div className="photo-container">
                                                            {!photosLoading && <div className="loading">
                                                                <Loader type="ThreeDots" color="#00bfff" height="80" width="80" />
                                                                </div> ||
                                                                <div className="soloPhoto">
                                                                    <img src={photo.thumbnailUrl}></img>
                                                                    <br/>
                                                                </div>
                                                            }
                                                            
                                                        </div>

                                                    )
                                                }
                                            } else {
                                                return null
                                            }
                                        })
                                    }
                                </div>
                                <br/>
                            </div>
                        </div>
                    }
                </div>
            </div>
        ))
        return (
            <>
                <div className="space-top"></div>

                <div className="albumsPage">
                    <h1 className="header-container">
                        <span className="text1">Albums from</span>
                        <span className="text2">jsonplaceholder</span>
                    </h1>

                    { albumItems }
                </div>
            </>
        )
    }
}


export default Albums