import React from 'react'
import Logo from './logo2.jpg'
import fire from '../fire'
import { NavLink } from 'react-router-dom'

const Header = () => {

    const handleLogout = () => fire.auth().signOut()

    return (
        <div>
            <nav className="navbar">
                <div className="max-width">
                    
                    <div className="logo">
                        <a href="https://swaraj-patil.github.io/picpano/">
                            <img src={Logo} alt="Logo"/>
                        </a>
                    </div>
                    <div className="menu">
                        <NavLink to="/home" className="menu-btn" exact activeClassName="active_menu">Home</NavLink>
                        <NavLink to="/images" className="menu-btn" activeClassName="active_menu">Images</NavLink>
                        <NavLink to="/posts" className="menu-btn" activeClassName="active_menu">Posts</NavLink>
                        <NavLink to="/albums" className="menu-btn" activeClassName="active_menu">Albums</NavLink>
                    </div>
                    <button className="logout-btn" onClick={handleLogout}>Logout</button>
                    
                    
                </div>
            </nav>
        </div>
        
    );
    
}

export default Header;